import React, {useEffect, useState} from 'react';


export default {
    title: 'useState demo',

}


export const Example = () => {
    const [counter, setCounter] = useState(0);

    useEffect(() => {
    setInterval(()=>document.title=counter.toString(),1000)
    },[counter])


    return (
        <>
            <button onClick={() => setCounter(counter + 1)}>+</button>
            {counter}


        </>
    )
};


